body {
    font: 14px "Century Gothic", Futura, sans-serif;
    background-color: #222;
    color: #fff;
}

.innerTable td {
    

}

.update > a{
    cursor: pointer;
}

.update > input{
    width: 75px;
}

td.add-delete {
    width: 80px !important;
    text-align: center;
}

td.add-delete span, tr > th:last-child span {
    cursor: pointer;
}

tr > th:last-child {
    text-align: right;
}

.right {
    text-align: right;
}

.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 15px;
    border-radius: 5px;   
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    -webkit-transition: opacity .2s;
    -o-transition: opacity .2s;
    transition: opacity .2s;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: #336699;
    cursor: pointer;
}

.slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #336699;
    cursor: pointer;
}

.graph{
    height: 800px;
}


